import React from "react"
import Img from "gatsby-image"
import styled from "styled-components"
import Image from "../components/image"
import SEO from "../components/seo"
import SectionContainer from "../components/SectionContainer"

const Container = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-grap: 0.5rem;
`

const StyledDiv = styled.div`
  font-size: 24px;
  padding: 3px;
  transform: rotate(-2deg);
  text-align: center;
  margin-bottom: -2rem;
  position: relative;
  z-index: 2;
  background-color: cornflowerblue;
  color: white;
`

function IndexPage({ data }) {
  const nodes = data.sections.nodes
  return (
    <>
      <SEO title="Home" />
      <h1>Welcome</h1>
      <p>Learn about the historical Monte Rosa / Neptune Lodge.</p>
      <Container>
        {nodes.map(v => {
          return (
            <SectionContainer link={`section/${v.slug.current}`}>
              <Img fluid={v.images[0].asset.fluid} alt={v.name} />
              <StyledDiv key={v.id}>{v.name}</StyledDiv>
            </SectionContainer>
          )
        })}
      </Container>
    </>
  )
}

export default IndexPage

export const pageQuery = graphql`
  query SectionQuery {
    sections: allSanitySection {
      nodes {
        slug {
          current
        }
        id
        name
        images {
          asset {
            fluid(maxWidth: 400) {
              ...GatsbySanityImageFluid
            }
          }
        }
      }
    }
  }
`
